<template>
  <div class="mainContent">
    <div class="formContent">
      <div class="formTitle">
        <img src="@/../static/img/headImg.png" alt>
        <div>{{userName}}, 初次登陆，请修改密码!</div>
      </div>
      <div class="formbody">
        <el-form :model="form" :rules="rules" ref="form">
          <el-form-item prop="oldPassword" label="原登录密码">
            <el-input v-model="form.oldPassword" placeholder="请输入原登录密码" type="password"></el-input>
          </el-form-item>
          <el-form-item prop="newPassword" label="新登录密码">
            <el-input
              v-model="form.newPassword"
              autocomplete="new-password"
              placeholder="设置6至20位新登录密码"
              type="password"
            ></el-input>
          </el-form-item>
          <el-form-item prop="confirmPassword" label="确认密码">
            <el-input
              type="password"
              v-model="form.confirmPassword"
              @keyup.enter.native="submit('form')"
              autocomplete="new-password"
              placeholder="请再次输入登录密码"
            ></el-input>
          </el-form-item>
        </el-form>
        <el-button type="primary" style="width: 100%; margin-top: 20px" @click="submit('form')">确定</el-button>
      </div>
    </div>
    <img src="@/../static/img/bottomCloud.png" class="bottomImg">
  </div>
</template>
<script>
import loginRequest from "@/api/login";
export default {
  name: "modifyPassword",
  data() {
    const checkNewPassword = (rule, value, callback) => {
      const reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,20}$/;
      if (!value) {
        callback(new Error("设置设置6至20位登录密码"));
      }
      if (!reg.test(value)) {
        callback(new Error("密码6至20位，必须包含字母与数字"));
      } else {
        callback();
      }
    };
    const checkConfirmPassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请再次输入登录密码"));
      }
      if (value === this.form.newPassword) {
        callback();
      } else {
        callback(new Error("两次密码不一致"));
      }
    };
    return {
      userName: "",
      form: {
        oldPassword: "",
        newPassword: "",
        confirmPassword: ""
      },
      rules: {
        oldPassword: [
          { required: true, message: "请输入原登录密码", trigger: "blur" }
        ],
        newPassword: [
          { required: true, validator: checkNewPassword, trigger: "blur" }
        ],
        confirmPassword: [
          { required: true, validator: checkConfirmPassword, trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData() {
      const url = loginRequest.userInfo;
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.userName = res.data.userName;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const newForm = JSON.parse(JSON.stringify(this.form));
          this.setPassword(newForm);
        }
      });
    },
    setPassword(formData) {
      const url = loginRequest.changePassword;
      this.$put(url, formData).then(res => {
        if (res.status === 1) {
          localStorage.removeItem("x_token");
          this.$notify.success({
            title: "success",
            duration: 1500,
            message: "修改密码成功请重新登录!"
          });
          this.$nextTick(() => {
            this.$router.push({
              path: "/"
            });
          });
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    }
  }
};
</script>
<style scoped>
.mainContent {
  width: 100%;
  background-color: #eef2f6;
  height: 100vh;
}
.mainContent .formContent {
  background-color: #fff;
  position: fixed;
  top: 25%;
  left: 50%;
  margin-left: -280px;
  z-index: 9999999999999;
}
.mainContent .formContent .formTitle {
  box-sizing: border-box;
  padding: 15px;
  width: 560px;
  height: 80px;
  background: url("../../assets/img/bg-chaxun.png") no-repeat;
  background-size: 100%;
}
.mainContent .formContent .formTitle img {
  float: left;
  width: 50px;
}
.mainContent .formContent .formTitle div {
  margin-left: 20px;
  float: left;
  text-align: left;
  line-height: 50px;
  font-size: 14px;
}
.mainContent .formContent .formbody {
  padding: 10px 20px 20px;
}
.mainContent .formContent .formbody .el-form-item {
  margin-bottom: 15px;
}
.mainContent .bottomImg {
  position: fixed;
  bottom: -150px;
  left: 0;
  width: 100%;
}
</style>



